import { call, put, takeEvery, all } from "redux-saga/effects";
import axios from "axios";
import { axiosResult, axiosError } from "./lyrics";

const INSERT_DATA = "sagas/INSERT_DATA"; // 이 액션으로 체킹해서 호출
const UPDATE_DATA = "sagas/UPDATE_DATA";
const DELETE_DATA = "sagas/DELETE_DATA";

export const insertData = () => ({ type: INSERT_DATA, payload: "insert" });
export const updateData = () => ({ type: UPDATE_DATA, payload: "update" });
export const deleteData = () => ({ type: DELETE_DATA, payload: "delete" });

const getAxiosData = payload =>
  axios.put(`http://localhost:3001/api/${payload}`);

// dispatch => checking =>
function* getAsyncData(action) {
  try {
    const data = yield;
    yield put(axiosResult(data));
  } catch (error) {
    yield put(axiosError());
  }
}

function* testSaga() {
  yield takeEvery(GET_ASYNCDATA, getAsyncData);
}

export function* rootSaga() {
  yield all([testSaga()]);
}

export default rootSaga;
